import * as LoginApi from '../../api/moudle/loginApi'
import TokenMgr from '../../mgr/TokenMgr'
import {saveUserInfo} from '../../mgr/userMgr'
import utils from '../../common/js/utils'

export default {
  data() {
    return {
      username: '',
      password: '',
      isLogin: false,
      errorInfo: '',
      showType: 'login',
      email: '',
      showLoading: false
    }
  },
  created() {
    this.$nextTick(() => {
    })
  },
  components: {},
  methods: {

    onSubmit() {
      if (this.showType === 'login') {
        this.login()
      } else if (this.showType === 'register') {
        this.regist()
      } else {
        this.requestPassword()
      }
    },
    onRegister() {
      this.showType = this.showType === 'register' ? 'login' : 'register'
    },
    onForget() {
      this.$prompt('请输入邮箱,我们会发送新密码到您到邮箱', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '邮箱格式不正确'
      }).then(({ value }) => {
        // this.$message({
        //   type: 'success',
        //   message: '你的邮箱是: ' + value
        // })
        this.email = value
        this.requestPassword()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        })
      })
    },
    login() {
      if (this.username.length === 0) {
        this.errorInfo = '* 用户名不能为空'
        return
      }
      if (this.password.length === 0) {
        this.errorInfo = '* 密码不能为空'
        return
      }
      this.errorInfo = ''
      let body = {
        'username': this.username,
        'password': this.password
      }
      this.showLoading = true
      LoginApi.login(body)
        .then(response => {
          console.log(response)
          this.showLoading = false
          // 存储token
          TokenMgr.add(this.axios.baseURL, response.data.token)
          let user = {
            'userName': this.username,
            'userId': response.data._id,
            'teamArr': response.data.teams,
            'email': response.data.email
          }
          // 保存用户信息
          saveUserInfo(user)
          this.$router.push('/')
        }, reject => {
          this.showLoading = false
        })
    },
    regist() {
      if (this.username.length === 0) {
        this.$message.error('用户名不能为空')
        return
      }
      if (this.email.length === 0) {
        this.$message.error('邮箱不能为空')
        return
      }
      if (!utils.valideEmail(this.email)) {
        this.$message.error('邮箱格式错误')
        return
      }
      if (this.password.length === 0) {
        this.$message.error('密码不能为空')
        return
      }
      let body = {
        'username': this.username,
        'password': this.password,
        'email': this.email
      }
      this.showLoading = true
      LoginApi.register(body)
        .then(response => {
          this.showLoading = false
          console.log(response)
          this.$message({
            message: '恭喜你，注册成功',
            type: 'success'
          })
          setTimeout(() => {
            this.onRegister()
          }, 500)
        }, reject => {
          this.showLoading = false
        })
    },
    requestPassword() {
      if (this.email.length === 0) {
        this.$message.error('邮箱不能为空')
        return
      }
      LoginApi.resetPassword({ email: this.email })
        .then(response => {
          console.log(response)
          this.$message({
            message: '密码已重置,新密码已通过邮件发送至您的邮箱.',
            type: 'success'
          })
        }, reject => {
          console.log(reject)
          this.$message.error(reject)
        })
    }

  }
}




